.preview {
    padding: 30px;
    min-height: calc(100vh - 66px);
}

.full_view .preview {
    padding: 90px 30px 30px;
}

.main_preview {
    min-height: calc(100vh - 186px);
}

.preview .device {
    position: relative;
    width: 680px;
    height: 400px;
    margin: 0 auto;
    background-color: #fff;
    border-radius: 3px;
    box-shadow: 0 5px 20px 0 rgb(32 48 60 / 11%);
}

.preview .device,
.preview .device:after,
.preview .device iframe {
    transition: all 0.5s ease;
}

.preview .device iframe {
    transform: scale(0.8);
}

.preview .device:after {
    content: " ";
    background-color: #444;
    position: absolute;
}

.preview .device {
    overflow: hidden;
}

.preview .device-desktop {
    width: 90%;
    height: calc(100vh - 190px);
    padding: 1em 1em;
    min-height: 550px;
    max-width: 1600px;
}

.preview iframe {
    width: calc(100% + calc(100%)*.250);
    height: calc(100% + calc(100%)*.250)
}

.preview .device-tablet-p {
    width: 80%;
    height: calc(100vh - 190px);
    padding: 1em;
    min-height: 650px;
    max-width: 650px;
}

.preview .device-mobile-p {
    width: 50%;
    height: calc(100vh - 186px);
    min-height: 430px;
    max-width: 375px;
    max-height: 667px;
    padding: 0.8em;
}

.preview iframe {
    margin: 0;
    border: 0;
    transform-origin: 0 0;
}

.devices {
    list-style: none;
    width: 100%;
    text-align: center;
    border-bottom: 1px solid #eee;
    margin: 0 0 2rem;
    padding: 0;
}

.devices>li {
    display: inline-block;
    line-height: 2em;
    margin: 0 1em 0 0;
}

.preview_header {
    height: auto;
    top: 0;
    left: 0;
    right: 0;
    box-shadow: none;
    background-color: #ffffff;
    border-top: 1px solid #EBEDF3;
    margin: 0;
}

.btn.btn-icon.btn-lg.preview_btn {
    height: calc(55px + 2px);
    width: calc(55px + 2px);
}

.p_wall_title {
    border-bottom: 1px solid #EBEDF3;
}

@media (max-width: 1440px) {
    .preview .device-desktop {
        width: 100%;
        height: calc(100vh - 186px);
        padding: .5em;
        min-height: 500px;
        max-width: 1200px;
    }

    .full_view .preview {
        padding: 85px 15px 15px;
    }

    .preview .device-tablet-p {
        width: 70%;
        height: calc(100vh - 186px);
        padding: .5em;
        min-height: 500px;
        max-width: 450px;
    }

    .preview .device-mobile-p {
        height: calc(100vh - 186px);
        min-height: 360px;
        max-width: 375px;
    }
}

@media (max-width: 1366px) {

    .preview .device-desktop,
    .preview .device-tablet-p {
        height: calc(100vh - 260px);
    }

    .preview .device-mobile-p {
        width: 80%;
    }

}

@media (max-width: 1366px) {
    .full_view .preview {
        padding: 115px 15px 15px;
    }
}

.p_wall_title h2 {
    max-width: 80%;
    text-overflow: ellipsis;
    width: 100%;
}

.preview_btn.exit-full svg {
    transform: rotate(180deg);
}

@media (min-width: 576px) {
    .p_device_btn {
        -webkit-box-pack: center;
        -ms-flex-pack: center;
        justify-content: center;
    }

    .p_wall_title {
        border-bottom: none;
    }
}

@media (max-width: 575px) {
    .btn.btn-icon.btn-lg.preview_btn {
        height: calc(48px + 2px);
        width: calc(48px + 2px);
    }
}