.add-feed-modal .themesNowThumbs ul {
    list-style: none;
    padding: 0;
    width: 100%;
    margin: 0 auto;
    text-align: left;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.add-feed-modal .themesNowThumbs ul li {
    display: inline-block;
    vertical-align: top;
    transition: all ease .3s;
    -webkit-transition: all ease .3s;
    -moz-transition: all ease .3s;
    transform: scale(1,1);
    -webkit-transform: scale(1,1);
    -moz-transform: scale(1,1);
    -webkit-box-flex: 0;
    -ms-flex: 0 0 33.33%;
    flex: 0 0 33.33%;
    max-width: 33.33%;
    padding-right: 15px;
    padding-left: 15px;
}

.add-feed-modal .themesNowThumbs ul li {
    transition: .38s linear;
    margin-bottom: 30px;
    padding-right: 20px;
    padding-left: 20px;
}

.add-feed-modal .themesNowThumbs ul li button {
    padding: 0;
    border: 0;
    background: none;
    width: 100%;
    display: block;
    color: #333;
}

.add-feed-modal .themesNowThumbs ul li span {
    display: block;
    font-size: 13.5px;
    font-weight: 500;
    white-space: nowrap;
    text-transform: uppercase;
    font-family: inter,sans-serif;
    font-weight: 600;
    text-align: left;
}

.add-feed-modal .themesNowThumbs ul li button span.imgThumb {
    background: #f5f5f5;
    padding: 20px 20px 10px;
    margin-bottom: 10px;
    border: 1px solid #dedede;
    border-radius: 5px;
}

.add-feed-modal .themesNowThumbs ul li button label input[type=radio] {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    visibility: hidden;
    display: initial;
}

.add-feed-modal .themesNowThumbs ul button.active {
    transform: scale(1);
    -moz-transform: scale(1);
    -webkit-transform: scale(1);
}

.add-feed-modal .themesNowThumbs .form-group.form-md-radios ul li button label {
    position: relative;
    margin: 0;
    display: block;
    cursor: pointer;
    color: #333;
}

.add-feed-modal .top-bar {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 9;
    width: 100%;
    background: #dbdbdb;
    border-radius: 5px 5px 0 0;
}

.add-feed-modal .top-bar img {
    width: 100%;
    display: block;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
}

.add-feed-modal .themesNowThumbs ul li button span.imgThumb img {
    width: 100%;
    height: auto;
}

.add-feed-modal .add-feed-modal-bigthumb {
    background: #f5f5f5;
    padding: 40px 20px 20px;
    margin-bottom: 20px;
    border-radius: 5px;
}

.add-feed-modal.active-scale .add-feed-modal-bigthumb {
    animation: fadeIn ease 1s;
    -webkit-animation: fadeIn ease 1s;
    -moz-animation: fadeIn ease 1s;
    -o-animation: fadeIn ease 1s;
    -ms-animation: fadeIn ease 1s;
}

.add-feed-modal .add-feed-devices li {
    display: inline-block;
    margin-right: 10px;
    margin-bottom: 20px;
}

.add-feed-modal .add-feed-devices li span {
    background-color: #ededed;
    color: #333;
    padding: 6px 16px;
    border-radius: 4px;
    font-size: 12px;
}

.add-feed-modal-bigthumb img{
    width:100%;
    height: auto;
}

.add-feed-modal .add-feed-devices {
    padding-bottom: 40px;
    border-bottom: 1px solid #dedede;
    margin-bottom: 20px;
}

.themesNowThumbs ul li.active button span, .themesNowThumbs ul li:hover button span {
    color: #5354FC;
    filter: blur(0);
}

.themesNowThumbs ul li:hover button span.imgThumb img {
    opacity: .9;
}

@media (max-width: 991px){
    .add-feed-modal .themesNowThumbs ul li {
        flex: 0 0 33.33%;
        max-width: 33.33%;
    }
}

@media screen and (max-width: 767px){
    .add-feed-modal .themesNowThumbs ul li {
        flex: 0 0 50%;
        max-width: 50%;
    }
}

@media (max-width: 575px){
    .add-feed-modal .themesNowThumbs ul li {
        -webkit-box-flex: 0;
        -ms-flex: 0 0 100%;
        flex: 0 0 100%;
        max-width: 100%;
    }
}