.dashboard-tabs li button{ 
    text-align: center; 
    width:100%;
}

ul.dashboard-tabs li button {
    border: 1px solid #ebedf3;
    border-radius: 4px;
    padding: 0 10px!important;
    background-color: #f2f6f8;
}

ul.dashboard-tabs li button:hover, ul.dashboard-tabs li button.active {
    box-shadow: 0 0 0 1.5px #959595;
    border-color: transparent;
    background-color: #fff;
}

.following_steps .embed_code .emebed_action {
    position: absolute;
    right: 25px;
    bottom: 32px;
    z-index: 5;
}

.publish_steps .tags-list ul li button {
    margin-left: 5px;
    border: 0;
    padding: 0;
    background: no-repeat;
}

.publish_steps .tags-list ul li button i {
    font-size: 10px;
}