/*NEW PRICE PAGE DESIGN START*/

.plans-page-active .payment_modal_panel {position: absolute; z-index: 96; width: 100%; top: 0; background-color: #f2f6f8; min-height: 100%; left: 0; right: 0;}
.pay_details {width: 100%;}
.pay_details { position: relative; overflow-x: hidden; height: calc(100vh - 65px); overflow-y: auto; padding-bottom: 30px;}
.pay_details.dashPayModal {margin-top: 0;}
.pay_details button.closeWizard {top: 0;}
button.closeWizard { position: absolute; background-color: transparent; border: none; z-index: 11; right: 30px ;  top: 30px;  opacity: .5;  transition: all .3s;}
.custom_pay_modal { position: relative; z-index: 3; min-height: 80vh;}

.price_container .main_title { margin-top: 50px; display: block; position: relative; z-index: 1; text-align: center; margin-bottom: 60px;}
.NormalPricing { max-width: 1170px; margin: 0 auto;}
.product_pricing_list .main_title { margin-bottom:40px;}
.product_pricing_list  .main_title h1 { color: #333333; font-size: 36px; margin-bottom: 5px;}
.product_pricing_list  .main_title p {color: #333333; font-size: 18px;}
.toggle_button { margin: 35px auto 0; max-width: 330px;}
.toggle {text-align: center; border: 1px solid #ddd; border-radius: 8px; padding: 5px;}
.toggler{padding: 10px 25px; border-radius: 8px; font-size: 16px; font-weight: 600; margin-bottom: 0; cursor: pointer;}
.toggler--is-active {background: #E3E3FC;  color: #5354FC; }
#filt-anually span { background: #E2F8E7; padding: 5px; border-radius: 4px; font-size: 13px; font-weight: 400; color: #40A059; margin-left:5px;}
#filt-anually.toggler--is-active span {background: #fff;}
.product_pricing_list .price_counter.price_card_wrap { width: 100%; display: flex; padding: 0; padding-top:18px; height: 100%; flex-direction: column;}
.product_pricing_list .price_head {padding: 30px 60px 0px; margin-bottom: 20px;}
.product_pricing_list .price_head h3, .pro_head h3 {font-size: 24px; font-weight:600;}
.product_pricing_list .price_head p {line-height: 1.6; font-size: 15px; padding: 0 25px; margin-bottom: 0;}
.product_pricing_list .price_wrap> div {display: inline-block; font-family: 'Barlow', sans-serif;}
.product_pricing_list .price_wrap .currency {font-size: 38px; font-weight: 600; position:relative; top: -2px; color: #333;}
.product_pricing_list .price_value {font-size: 43px; font-weight: 600; color: #333;}
.product_pricing_list .month {color: #878787; font-size: 18px; position: relative;}
.product_pricing_list .price_body { padding: 0 35px; height: 100%; display: flex; flex-direction: column;}
.product_pricing_list .price_body h6{ font-size: 16px; font-weight:600; margin-bottom: 15px;}
.product_pricing_list .pricingList { height: 100%; padding-left: 0;}
.product_pricing_list .pricingList li {margin-bottom: 10px; font-size: 15px; position: relative; padding-left: 18px; line-height: 1.5; }
.product_pricing_list .pricingList li:before{ position: absolute; content: ""; width:6px; height:6px; left:0; top:9px; background: #b9b6b6;; border-radius: 100%;}
.product_pricing_list .pricingList li .p_tooltip { display: none;}
.product_pricing_list .growth_plan .price_cardT.card {border-radius: 9px 0 0 9px; border-right:none; }
.product_pricing_list .premium_plan .price_cardT.card { border-radius: 0 9px 9px 0; border-left: none;}
.product_pricing_list .professional_plan .price_cardT.card{ border-radius: 0;}
.product_pricing_list .price_cardT.card { height: 100%; padding-bottom: 26px;}
.product_pricing_list .professional_plan .price_cardT.card .most_popular {position: absolute; left: 50%; top: -15px; background: #E3E3FC; color: #5354FC; font-size: 14px; font-weight: 600; padding: 5px 10px; border-radius: 4px; transform: translate(-50%, 0);}
.product_pricing_list .price_btn{ max-width: 200px; margin: 0 auto 20px;}
.product_pricing_list .splide__pagination { display: none !important;}
.product_pricing_list .price_head .priceSwicher{margin: 0;}

.shopify.product_pricing_list .price_btn{ max-width: 200px; margin: 0 auto 20px;}
.shopify.product_pricing_list .price_head {padding: 30px 15px 0;}
/* .shopify .free_plan .pricingList { padding-top: 34px;} */
.shopify.product_pricing_list .price_head {padding: 30px 15px 0;}
.shopify.product_pricing_list .free_plan .price_btn { text-align: center;}
.shopify.product_pricing_list .growth_plan .price_card.card { border-radius: 0px; border-right: none; border-left: none;}
.shopify.product_pricing_list .price_counter.price_card_wrap.free_plan .price_card.card { border-radius: 9px 0 0 9px;}
.shopify.product_pricing_list .price_body{padding: 0 25px;}
.activeFreePlan {position: absolute; left: -1px; top: -1px; background: #E2F8E7; color: #40A059; font-weight: 600; padding: 4px 12px; border-radius: 0px 0 10px 0; font-size: 13px;}
.growth_plan .activeFreePlan, .free_plan .activeFreePlan{border-radius: 10px 0 10px 0;}
.shopify.product_pricing_list .growth_plan .activeFreePlan{border-radius: 0px 0 10px 0;}
.product_pricing_list .price_cardT.card{ border-radius: 9px 0 0 9px;}
.product_pricing_list .free_plan .price_cardT.card { border-right: none; border-radius:9px 0 0 0; }

.price_note {border-radius: 0 0 9px 9px; text-align: center; border: 1px solid #ebedf3; background: #fff; border-top: none; padding: 10px 15px;  margin: 0 auto;}
.product_pricing_list .premium_plan .price_cardT.card { border-radius: 0 9px 0 0;}
.product_pricing_list .growth_plan .price_cardT.card{border-radius: 0;}
.product_pricing_list .growth_plan .price_cardT.card {border-radius: 9px 0 0;}

.shopify.product_pricing_list .growth_plan .price_cardT.card { border-radius: 0;}

.pricing_bottom.flex-row{ padding:20px; background: #fff; border-radius: 4px; border:1px solid #eee; margin: 0; margin: 15px 0 0;}
.pricing_bottom p {font-size: 16px; max-width: 740px;}
.pricing_bottom .btn { padding: 0.65rem 3rem; font-size: 16px;}

.price_btn label {background-color: #5354fc; padding: 10px 15px; border-radius: 4px; color: #fff; display: block; cursor: pointer; margin-bottom: 0;}

/* DISPLAY PRICE CSS START */

/* .DisplaySignage {max-width: 1024px;} */
.socialPagePricing .flex-col-3.col-md-4, .socialPagePricing .flex-col-4.col-md-3 { margin-bottom: 2rem;}
.price_card { color: #333; text-align: center; background-color: #fff; margin: 0 auto; box-shadow: 0px 4px 10px #0000001A; border: 1px solid #C6C6C6; border-radius: 4px; overflow: visible; max-width: initial;}
.socialPagePricing .price_card { padding: 50px 20px; height: 100%; padding-bottom: 70px; position: relative;}
.color1 {color: #333;}
.price_card h3 { margin-top: 10px;}
.f2 {font-size: 16px;}
.socialPagePricing .price_card .price_wrap {margin-bottom: 1.5rem; margin-top: 2rem;}
.price_wrap h2 {margin: 0; font-size: 48px; font-weight: bold; line-height: 44px; display: flex; align-items: baseline; justify-content: center;}
.price_wrap h2 sup { top: -14px; font-size: 60%;}
.price.count {transition: all .5s;}
.price_wrap h2 .valid_from { font-size: 26px; text-transform: lowercase; color: #848484;}
.pricingList { text-align: left; padding-left: 10px; list-style: none;}
.pricingList li { font-weight: 400; color: #333; line-height: 32px; font-size: 14px; position: relative; padding-left: 20px;}
.pricingList li:before { position: absolute; content: ""; background: url(https://s3.us-west-1.wasabisys.com/cloud.taggbox.com/taggshop/assets/images/price-page/check-icon-purple.svg);
width: 8px; height: 8px; left: 0px; top: 13px; background-size: 100%; background-repeat: no-repeat;}
.iconCheckClose {margin-right: 5px;}
.pricingList li span.iconCheckClose {position: absolute; left: 0px;}
.socialPagePricing .price_card .price_btn {margin: 0 30px; position: absolute; left: 0; right: 0; bottom: 20px; width: auto;}
.price_btn label {background-color: #5354fc; padding: 10px 15px; border-radius: 4px; color: #fff; display: block; cursor: pointer; margin-bottom: 0;}
.price_btn input[type="radio"] {display: none;}
/* .highlightCard {top: -34px;} */
.mostpopularSticky {text-align: center; background: #5354fc; position: initial; font-size: 18px; border-radius: 4px 4px 0px 0px; margin: 0; font-weight: 700; color: #fff; top: -28px;
padding: 8px 18px; width: 100%; line-height: 19px;}
.price_card.highlightboxCardBorder { /*height: calc(100% - 15px); padding-top: 36px;*/ border: 1px solid #5354fc; border-radius: 0 0 4px;}
.flex-row {justify-content: center; display: -webkit-box; display: -ms-flexbox; display: flex; -ms-flex-wrap: wrap; flex-wrap: wrap; margin-right: -12.5px; margin-left: -12.5px;}
.p_tooltip {margin-left: 5px;}
.price_container .pricingList .p_tooltip { position: relative;}
.p_tooltip svg {width: 14px; height: 14px;}


.features_table_wrap { padding-top: 20px; width: 100%; position: relative; z-index: 1;}
.features_table { background-color: #fff;}
.features_table .table {background-color: #fff; margin-bottom: 0;}
.features_table .table tr th:first-child, .features_table .table tr td:first-child {text-align: left;}
.features_table .table tr th h4 {font-size: 18px; margin: 7px 0; font-weight: bold;}
.features_table .table tr th, .features_table .table tr td {text-align: center; border-color: #EBEBEB; padding: 15px 20px;}
.badge-red { display: inline-block; margin-left: 5px; background: #5354fc; padding: 1px 8px; line-height: normal; font-size: 12px; color: #fff; border-radius: 4px; font-weight: 500;}
.features_btn_wrap {text-align: center; display: block; padding: 20px 0;}

.colorActive {color: #5354fc !important;}
.platform__icons.colored_social_icons li{padding-left: 0;}
.platform__icons.colored_social_icons li:before {display: none;}
.price_container .tooltip__content {visibility: hidden; opacity: 0; display: flex; justify-content: center; flex-wrap: wrap; padding: 2px 10px; transition: all 0.3s ease;
position: absolute; bottom: 100%; left: 50%; transform: translateX(-50%); min-width: 160px; background-color: #fff; box-shadow: 2px 3px 8px rgba(0,0,0,.2);}
ul.platform__icons, ul.platform__icons ul {padding: 0; list-style: none;}
ul.platform__icons li {display: inline-block; vertical-align: middle; margin: 0 3px; position: relative;}
ul.platform__icons li svg, ul.platform__icons li i {vertical-align: middle; display: block; line-height: 30px;}
ul.platform__icons li:hover ul.platform__more_ {top: 100%; opacity: 1; visibility: visible; transition: all 0.3s ease;}
.price_container .pricingList .p_tooltip:hover .tooltip__content {visibility: visible; opacity: 1; transition: all 0.3s ease;}
ul.platform__icons li ul {position: absolute; background-color: #fff; padding: 8px; min-width: 170px; box-shadow: 0 0 12px rgba(0,0,0,.1); border-radius: 3px; right: -18px;
left: auto; top: 110%; opacity: 0; visibility: hidden; transition: all 0.3s ease; margin: 10px auto; text-align: center; z-index: 9;}


.colored_social_icons .fa-facebook {color: #1878F2;}
.colored_social_icons .fa-twitter {color: #26c4f1;}
.colored_social_icons .fa-instagram {color: #cd486b;}
.colored_social_icons .fa-facebook {color: #1878F2;}
.colored_social_icons .fa-linkedin {color: #0e76a8;}
.colored_social_icons .fa-rss {color: #ee802f;}
.colored_social_icons .fa-flickr {color: #ff0084;}
.colored_social_icons .fa-pinterest {color: #bd081c;}
.colored_social_icons .fa-vimeo {color: #1ab7ea;}
.colored_social_icons .fa-tumblr {color: #35465c;}
.colored_social_icons .fa-youtube {color: #e62117;}
.colored_social_icons .fa-google {color: #DD3D28;}
.colored_social_icons .fa-yelp {color: #BD0301;}
.colored_social_icons .fa-yammer {color: #255190;}
.colored_social_icons .fa-slack {color: #481449;}
.colored_social_icons .fa-airbnb {color: #F7565D;}
.colored_social_icons .fa-soundcloud {color: #F85204;}
.colored_social_icons .fa-workplace {color: #677B8C;}
.colored_social_icons .fa-giphy {color: #110F17;}

/* DISPLAY PRICE CSS END */



